import { useFormik } from "formik";
import { useState } from "react";
import { useDispatch } from "react-redux";
import Input from "../../components/Input";
import { editMeal } from "../../redux/mealSlise";
import { useEventListener } from "../../utils/eventListener";
import { prepareValues } from "../../utils/math";
import { MealPopupProps } from "../../utils/types";
import { onKeyPress } from "../../utils/utils";
import { mealValidationSchema } from "../../utils/validation";
import {
  Close,
  Data,
  SavedData,
  EditButton,
  Flex,
  Form,
  Grid,
  MealPopupContainer,
  MealPopupOverlay,
  SaveButton,
  Text,
  Label,
} from "./MealPopup.style";

function MealPopup({ data, isOpen, onClose, onKeydown }: MealPopupProps) {
  const dispatch = useDispatch();

  const [editedForm, setEditedForm] = useState(false);

  useEventListener(isOpen, onKeydown, "keydown");

  const formik = useFormik({
    initialValues: {
      name: data?.name!,
      startWeight: 100,
      protein: Math.round((data?.protein! * 100) / data?.weight!),
      fat: Math.round((data?.fat! * 100) / data?.weight!),
      carb: Math.round((data?.carb! * 100) / data?.weight!),
      weight: data?.weight!,
      day: new Date(data?.timestamp!).getDate(),
      month: new Date(data?.timestamp!).getMonth() + 1,
      year: new Date(data?.timestamp!).getFullYear(),
    },
    enableReinitialize: true,
    validationSchema: mealValidationSchema,
    onSubmit: (values, { resetForm }) => {
      dispatch(
        editMeal({ ...values, id: data!.id, calories, day, month, year })
      );
      onClose();
      setEditedForm(false);
      resetForm();
    },
  });

  const { name, startWeight, protein, fat, carb, weight, day, month, year } =
    formik.values;

  const calories = prepareValues(formik.values);

  return (
    <MealPopupOverlay opened={isOpen} onClick={onClose}>
      <MealPopupContainer opened={isOpen} onClick={(e) => e.stopPropagation()}>
        <Close
          onClick={() => {
            onClose();
            setEditedForm(false);
          }}
        ></Close>
        {editedForm ? (
          <Form onSubmit={formik.handleSubmit}>
            <Input
              formik={formik}
              value={name}
              name="name"
              placeholder="Название"
              type={"text"}
            />
            <Flex>
              <Text>БЖУ на</Text>
              <Input
                formik={formik}
                value={startWeight}
                name="startWeight"
                placeholder="-Граммы"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
                startWeight={true}
              />
              <Text>грамм</Text>
            </Flex>
            <Grid>
              <Input
                formik={formik}
                value={protein}
                name="protein"
                placeholder="Белки"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
              <Input
                formik={formik}
                value={fat}
                name="fat"
                placeholder="Жиры"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
              <Input
                formik={formik}
                value={carb}
                name="carb"
                placeholder="Углеводы"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
            </Grid>
            <Input
              formik={formik}
              value={weight}
              name="weight"
              placeholder="Граммы"
              type={"number"}
              onKeyPress={(evt) => onKeyPress(evt, formik)}
            />
            <Flex>
              <Text>Дата:</Text>
            </Flex>
            <Grid>
              <Input
                formik={formik}
                value={day}
                name="day"
                placeholder="День"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
              <Input
                formik={formik}
                value={month}
                name="month"
                placeholder="Месяц"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
              <Input
                formik={formik}
                value={year}
                name="year"
                placeholder="Год"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
            </Grid>
            <SaveButton
              type="submit"
              aria-label="Сохранить изменение"
              disabled={!formik.isValid}
            >
              Сохранить изменение
            </SaveButton>
          </Form>
        ) : (
          <SavedData>
            <Data>{data?.name!}</Data>
            <Flex>
              <Text>Рассчитанные данные:</Text>
            </Flex>
            <Data>{data?.calories!} ккал</Data>
            <Grid>
              <Data>
                <Label>белки</Label>
                {data?.protein!} г.
              </Data>
              <Data>
                <Label>жиры</Label>
                {data?.fat!} г.
              </Data>
              <Data>
                <Label>углеводы</Label>
                {data?.carb!} г.
              </Data>
            </Grid>
            <EditButton
              type="button"
              aria-label="Редактировать"
              onClick={() => setEditedForm(true)}
            >
              Редактировать
            </EditButton>
          </SavedData>
        )}
      </MealPopupContainer>
    </MealPopupOverlay>
  );
}

export default MealPopup;
